/***********************UNIVERSAL STYLES***********************/
.container {
	display: -ms-flexbox;
	display: flex;
	width: 100%;
}

.space {
	padding: 10px;
}

.panel {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: start;
	    justify-content: start;
	-ms-flex-line-pack: start;
	    align-content: start;
	aligen-items: start;
	width: 95%;
	margin: 10px;
	padding: 5px;
}

.panel-form {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 100%;
	padding: 5px;
	margin: 10px;
}

.subcontainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	width: 50%;
	padding: 5px;
	margin: 10px;
}

.header {
	margin: 5px;
	margin-left: 0px;
	font-size: 30px;
	text-align: left;
}

.output {
	margin:1px;
}

/***********************LINEITEM STYLES***********************/
.container-lineitem {
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: row;
	    flex-direction: row;
}
.items-lineitem {
	margin: 5px;
}
.input-lineitem {
	width: 25%;
}

.header-lineitem {
	text-align: center;
	margin: 10px;
}
.delete-lineitem {
	color: red;
	padding: 5px;
}
.edit-lineitem {
	color: green;
	padding: 5px;
}



